body {
    background: #3494E6;
    background: -webkit-linear-gradient(to right, #EC6EAD, #3494E6);
    background: linear-gradient(to right, #EC6EAD, #3494E6);
    font-family: 'Saira Condensed', monospace;
    height: 90vh;
    overflow-y: auto;
    overflow-x: auto;
}


button {
    font-family: monospace;
    margin: 10px;
    padding: 10px 15px;
    text-align: center;
    text-transform: uppercase;
    color: white;
    border-radius: 10px;
    font-size: 1rem;
    font-weight: bold;
}

button:hover {
    cursor: pointer;
    border: 1px solid white !important;
}

#settings-panel button {
    background-color: #3190da;
    border: 1px solid #1a1a1a;
    padding: 8px 10px;
    border-radius: 25px;
}

#controls-panel button {
    background-color: #910981;
    border: 1px solid #1a1a1a;
    padding: 15px 25px;
    border-radius: 30px;
}

#clock-container {
    text-align: center;
    background: rgb(57,57,57);
    background: linear-gradient(180deg, rgba(57,57,57,1) 0%, rgba(73,73,73,1) 4%, rgba(0,0,0,1) 29%, rgba(0,0,0,1) 97%, rgba(71,71,71,1) 100%);
    width: 80vmin;
    margin: 10vh auto;
    padding: 16px;
    border-radius: 80px;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

#display-container {
    background: rgb(158,216,255);
    background: linear-gradient(156deg, rgba(158,216,255,1) 0%, rgba(199,244,255,1) 7%, rgba(251,232,252,1) 93%, rgba(237,193,240,1) 100%);
    color: #121418;
    font-size: 60px;
    border-radius: 60px;
    border: 15px solid #0a0e15;
}

#controls-panel {
    padding: 5px;
    margin: auto;
    color: white;
}

#settings-panel {
    padding: 7px;
    font-family: 'Saira Condensed', sans-serif;
    font-size: 1.2rem;
    background-color: #1d293b;
    border-radius: 50px;
    color: white;
    vertical-align: middle;
}

#break-label {
    width: 40vmin;
    margin-right: 10px;
}

#break-length {
    font-size: 1.6rem;
    background-color: black;
    border: 1px solid #0c161f;
    border-radius: 10px;
    padding: 0 10px 4px 10px;
}

#session-label {
    width: 30vmin;
    margin-left: 10px;
}

#session-length {
    font-size: 1.6rem;
    background-color: black;
    border-radius: 10px;
    padding: 0 10px 4px 10px;
}

#timer-label {
    text-transform: uppercase;
    font-size: 2.9rem;
    height: 3rem;
}

#time-left {
    font-size: 6rem;
    font-weight: bold;
    text-shadow: 2px 2px #c2e9f1;
}

#app-title {
    padding: 0;
    margin: 0;
    font-size: 0.9rem;
    font-family: monospace;
    font-weight: bold;
    text-shadow: 1px 1px 1px #6e6e6e;
}
